* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.mainContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px; 
    background-color: #f8f9fa; 
    min-height: 100vh; 
}

.navbar {
    z-index: 1041;
    box-shadow: 0 3px 6px rgba(50, 50, 50, 0.2);
    font-family: 'Josefin Sans', sans-serif;
    background-color: #30c3a8 !important;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 30px;
}

.image-preview {
    max-width: 100%;
    height: auto;
    border: 1px solid #ccc;
    margin-bottom: 20px; 
    border-radius: 8px; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
}

.image-placeholder {
    width: 100%;
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e9ecef;
    border: 1px dashed #007bff;
    border-radius: 8px;
    margin-bottom: 20px;
}

.upload-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
}

.upload-btn button,
.download-btn {
    margin-top: 10px;
    padding: 12px 25px;
    background-color: #7c17cf;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.upload-btn button:hover,
.download-btn:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}

.row {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 20px;
}

.col-xl-4,
.col-lg-4,
.col-md-12,
.col-sm-12 {
    flex: 1;
    max-width: 300px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btn-dark {
    background-color: #343a40;
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.btn-dark:hover {
    background-color: #495057;
}

@media (max-width: 768px) {
    .row {
        flex-direction: column;
    }
}
